import React, { useState, useCallback } from "react"
import Layout from "../layouts/layout"
import DatePicker from "react-datepicker";
import "../styles/style.scss"
import "react-datepicker/dist/react-datepicker.css";

export default function Home() {
  const [ myBirthDate, setMyBirthDate ] = useState(new Date());
  const [ dogsBirthDate, setDogsBirthDate ] = useState(new Date());
  const [ targetDate, setTargetDate ] = useState(null);

  const calculateDay = useCallback(() => {
    const diffTime = dogsBirthDate - myBirthDate;
    setTargetDate(new Date(dogsBirthDate.getTime() + diffTime / 6));
  }, [myBirthDate, dogsBirthDate]);

  return (
    <Layout>
      <h1>When will your dog be the same age as you?</h1>
      <div>
        <p>I was born on</p>
        <DatePicker
          selected={myBirthDate}
          onChange={date => setMyBirthDate(date)}
        />
      </div>
      <div>
        <p>My dog was born on</p>
        <DatePicker
          selected={dogsBirthDate}
          onChange={date => setDogsBirthDate(date)}
        />
      </div>
      <br />
      <button onClick={calculateDay}>
        Calculate
      </button>
      { targetDate != null && 
        <p>{`You and your dog will be the same age on ${targetDate.toISOString().slice(0,10)}`}</p>      
      }
    </Layout>
  );
}
